@import '../../assets/css/variable.less';
*{box-sizing: border-box;}
.footer{
  font-weight: 500;
  .hr{
    width: 91.5%;
    height: 1px;
    max-width: 800px;
    margin: 0 auto;
    background: linear-gradient(to right,rgba(255,0,0,0),rgba(255,255,255,0.5),rgba(255,0,0,0));
  }
}

.footer-box{
  display: flex;
  justify-content: space-between;
  width: 1063px;
  height: 50px;
  margin: 0 auto;
  margin-bottom: 32px;
  align-items: flex-end;
  .left{
    a{
      margin-left: 24px;
      color: @defaultFontColor;
    }
    .one-a{
      margin-left: 0;
    }
  }
  .contact{
    display: flex;
    justify-content : between-space;
    align-items : center;
    img {
      margin-left :16px;
    }
  }
  .mobile{
    display: none;
  }
}

@media only screen and (min-width : 1140px) and (max-width :1568px){
  .footer-box{
    width: 1013px;
  }
}

@media only screen and (min-width : 760px) and (max-width :1140px){
  .footer-box{
    width: 712px;
  }
}

@media only screen and (max-width :759px){
  .footer-box{
    width: 100%;
    min-width: 375px;
    padding: 0 24px;
    height: 71px;
    display: block;
    .pc-bn,.pc-con{
      display: none;
    }
    .left{
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 27px;
    }
    .mobile{
      display: flex;
      justify-content: space-between;
      margin-top: 24px;
    }
  }
}
