@import '../../assets/css/variable.less';

* {
  box-sizing: border-box;
}

.video {
  position: absolute;
  right: 0px;
  height: auto;  
  width: auto;  
  padding-bottom: 1%;
  z-index: 1;
  .safari-img{
    width: 100%;
    height: 100%;
  }
  video {
    width: 100%;
    height: 100%;
    object-fit: fill;
    source {
      height: 100%;
      width: 100%;
    }
  }
}



.home {
  font-weight: normal;

  .home-box {
    background: url('./img/home-bg.png');
    background-repeat: no-repeat;
    background-size: 100%;
    position: relative;
    .home-bg{
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: -1;
      
    }
  }

  .first-screen {
    height: calc(100vh - 72px);
    min-height: 620px;
    position: relative;
    background: url('./img/matrix-effect-without-animation.png');
    background-size: 100%;
    background-repeat: no-repeat;

    .down {
      text-align: center;
      position: absolute;
      bottom: 50px;
      left: 50%;
      transform: translateX(-50%);
    }

    .title {
      padding-top: 278px;
      margin-left: 168px;
      position: relative;
      z-index: 1;
      font-weight: normal;

      .title-text {
        font-size: 72px;
        color: #FFFFFF;
        letter-spacing: 5px;
      }

      .deri-describe {
        font-size: 24px;
        margin-left: 4px;
      }

      .button-home {
        margin-top: 69px;

        .start-trading,
        .read-our {
          display: inline-block;
          width: 256px;
          height: 64px;
          border-radius: 4px;
          text-align: center;
          line-height: 64px;
          font-size: 18px;
          font-weight: 800;
          background-color: #3756CD;
        }

        .read-our {
          margin-left: 48px;
          background: rgba(85, 119, 253, 0.1);
        }
      }
    }
  }

  .second-screen {
    .current-stats {
      height: 700px;
      background: url('./img/hr-bg.svg');
      background-repeat: no-repeat;

      .current-stats-title {
        text-align: center;
        font-size: 48px;
        color: #FFFFFF;
        padding-top: 31px;
        letter-spacing: 5px;
        position: relative;
        z-index: 9;
      }

      .current-stats-box {
        margin-top: 95px;
        display: flex;
        justify-content: center;

        .total-volume,
        .total-transactions {
          padding: 24px 48px;
          border: 1px solid;

          .num {
            font-size: 64px;
            color: #FFFFFF;
            white-space: nowrap;
            letter-spacing: 2px;
            font-weight: 300;
          }

          .title-name {
            font-size: 20px;
            letter-spacing: 2px;
            white-space: nowrap;
            font-weight: 600;
          }
        }

        .total-volume {
          width: 604px;
          height: 157px;
          margin-right: 80px;
          border-image: linear-gradient(to right, #FFFFFF, #5577FD, #5577FD, #8247E5) 1;
          background: linear-gradient(0deg, rgba(255, 255, 255, 0.03), rgba(255, 255, 255, 0.03)), linear-gradient(85.64deg, rgba(130, 71, 229, 0.07) 0.47%, rgba(85, 119, 253, 0.07) 24.56%, rgba(85, 119, 253, 0.07) 55.94%, rgba(255, 255, 255, 0.07) 103.87%);
        }

        .total-transactions {
          width: 334px;
          height: 157px;
          border-image: linear-gradient(to right, #8247E5, #CD7A37) 1;
          background: linear-gradient(0deg, rgba(255, 255, 255, 0.03), rgba(255, 255, 255, 0.03)), linear-gradient(61.79deg, rgba(205, 122, 55, 0.07) 7.46%, rgba(130, 71, 229, 0.07) 69.49%);
        }
      }
    }

    .who-uses-deri {
      margin-top: 156px;
      text-align: center;

      .who-uses-deri-title {
        font-size: 48px;
        color: #FFFFFF;
        letter-spacing: 5px;
      }

      .who-uses-deri-describe {
        width: 903px;
        margin: 0 auto;
        font-size: 24px;
        margin-top: 29px;
      }

      .who-uses-deri-box {
        margin: 0 auto;
        width: 1382px;
        height: 500px;
        margin-top: 61px;
        justify-content: space-between;
        display: flex;

        .role-logo {
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
          top: 0;
        }

        .deri-role {
          width: 296px;
          height: 500px;
          position: relative;
          display: flex;
          align-items: flex-end;

          .role-title-describe {
            padding: 0 20px;
            padding-top: 195px;
            height: 390px;
            background: url('./img/user-bg-1568.png');
            background-repeat: no-repeat;

            .role-title {
              height: 58px;
              font-size: 24px;
              color: #FFFFFF;
            }
          }
        }
      }
    }

    .how-to-start-trading {
      width: 1359px;
      height: 1354px;
      margin: 0 auto;
      margin-top: 219px;

      .how-to-start-trading-title {
        text-align: center;
        color: #FFFFFF;
        font-size: 48px;
        letter-spacing: 5px;
      }

      .step-connect-wallet,
      .step-deposit-margin,
      .step-trade-derivatives {
        margin-top: 155px;
        display: flex;
        justify-content: flex-end;
        position: relative;

        .step-logo {
          position: absolute;
        }

        .logo-one {
          left: 0;
          top: -100%;
        }

        .logo-two {
          right: -2%;
          top: -70%;
        }

        .logo-three {
          left: 0;
          top: -70%
        }

        .step-box {
          width: 1171px;
          height: 200px;
          border: 1px solid;
          padding-top: 41px;
          background: linear-gradient(0deg, rgba(255, 255, 255, 0.02), rgba(255, 255, 255, 0.02)), linear-gradient(85.64deg, rgba(130, 71, 229, 0.07) 0.47%, rgba(85, 119, 253, 0.07) 24.56%, rgba(85, 119, 253, 0.07) 55.94%, rgba(255, 255, 255, 0.07) 103.87%);
          border-image: linear-gradient(to right, #FFFFFF, #5577FD, #5577FD, #8247E5) 1;
        }

        .step-title {
          font-size: 24px;
          color: #FFFFFF;
          margin-left: 356px;
        }

        .step-describe {
          width: 732px;
          margin-top: 42px;
          margin-left: 356px;
        }
      }

      .step-deposit-margin {
        .step-title {
          margin-left: 98px;
        }

        .step-describe {
          width: 638px;
          margin-left: 98px;
        }
      }

      .step-hr-one,
      .step-hr-two {
        margin-top: 39px;
        margin-left: 156px;

        .hr-one {
          width: 1118px;
          height: 1px;
          margin: 0 auto;
          background: linear-gradient(to right, rgba(255, 0, 0, 0), rgba(255, 255, 255, 0.5), rgba(255, 0, 0, 0));
        }

        .hr-tow {
          width: 456px;
          height: 1px;
          margin: 0 auto;
          margin-top: 44px;
          background: linear-gradient(to right, rgba(255, 0, 0, 0), rgba(255, 255, 255, 0.5))
        }

        .hr-three {
          width: 666px;
          height: 1px;
          margin: 0 auto;
          margin-top: 28px;
          background: linear-gradient(to right, rgba(255, 255, 255, 0.5), rgba(255, 0, 0, 0))
        }
      }

      .step-hr-two {
        margin-top: 71px;
      }
    }

    .backed-by {
      margin-top: 108px;

      .backed-by-title {
        text-align: center;
        font-size: 48px;
        color: #FFFFFF;
        letter-spacing: 5px;
      }

      .backed-by-box {
        margin: 0 auto;
        margin-top: 72px;
        width: 777px;
        height: 199px;
        padding: 0 24px;
        img{
          width: 102px;
          height: 80px;
        }
        div {
          display: flex;
          justify-content: space-between;
          margin-top: 8px;
        }

      }
    }

    .our-partners {
      margin-top: 136px;

      .our-partners-title {
        text-align: center;
        font-size: 48px;
        color: #FFFFFF;
        letter-spacing: 5px;
      }

      .our-partners-box {
        margin: 0 auto;
        margin-top: 72px;
        width: 304px;
        height: 80px;
        padding-bottom: 95px;

        div {
          display: flex;
          justify-content: space-between;
          margin-top: 8px;
        }
      }
    }
  }
}

